.about-us {
	padding-top: 20px;
	padding-bottom: 30px;

	&__block {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 30px;
		width: 100%;

		@media screen and (min-width: 767px) {
			flex-direction: row;
			align-items: stretch;
		}
	}

	&__img {
		display: inline-block;
	}

	&__information {
		@media screen and (min-width: 768px) {
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 8px;
		text-align: center;

		font-weight: 500;
		font-size: 24px;
		line-height: 1.21;
		color: $title-text-color;

		@media screen and (min-width: 768px) {
			margin-bottom: 28px;
			text-align: left;
			font-size: 32px;
		}
	}

	&__text {
		margin: 0;

		line-height: 1.25;
		color: $title-text-color;

		&--hidden {
			display: none;
		}
	}

	&__link {
		display: flex;

		margin-top: 30px;
		font-weight: 500;
		line-height: 1.25;
		color: $title-text-color;

		@media screen and (min-width: 1200px) {
			display: none;
		}
	}
}
.indent {
	margin-top: 30px;
	@media screen and (min-width: 1200px) {
		margin-bottom: 30px;
	}
}
