.wrapper {
	background-image: url(../images/background-480.jpg);
	background-size: cover;
	background-repeat: no-repeat;

	@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
		background-image: url(../images/background-480@2x.jpg);
		background-size: cover;
		background-repeat: no-repeat;
	}

	@media screen and (min-width: 768px) {
		background-image: url(../images/background-768.jpg);
		background-size: cover;
		background-repeat: no-repeat;

		@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
			background-image: url(../images/background-768@2x.jpg);
			background-size: cover;
			background-repeat: no-repeat;
		}
	}
	@media screen and (min-width: 1200px) {
		background-image: url(../images/background-1200.jpg);
		background-size: cover;
		background-repeat: no-repeat;
		@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
			background-image: url(../images/background-1200@2x.jpg);
			background-size: cover;
			background-repeat: no-repeat;
		}
	}
}
.header {
	display: flex;
	align-items: center;
	margin: 0;
	padding: 0;

	&__picture {
		z-index: 2;
		margin: 16px 0 20px 20px;

		@media screen and (min-width: 768px) {
			margin: 24px 0 32px 28px;
		}

		@media screen and (min-width: 1200px) {
			margin: 36px 0 65px 16px;
		}
	}
	@media screen and (max-width: 1199px) {
		justify-content: space-between;
		align-items: baseline;
	}
}

.menu-button {
	display: none;
	width: 30px;
	height: 16px;
	margin-right: 20px;
	padding: 0;
	border: none;
	background-color: rgba(0, 0, 0, 0);
	z-index: 2;

	@media screen and (min-width: 768px) {
		margin-right: 32px;
	}

	@media screen and (max-width: 1199px) {
		display: flex;
	}
}
.menu-button .icon-close {
	display: none;
}
.menu-button.is-open .icon-close {
	display: block;
}
.menu-button.is-open .icon-menu {
	display: none;
}

.menu-container {
	position: fixed;
	display: none;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	padding-top: 112px;
	text-align: center;
	background-color: #fbfbfb;
	z-index: 1;
	overflow-y: scroll;

	@media screen and (min-width: 768px) {
		padding-top: 140px;
	}

	&.is-open {
		display: flex;
		flex-direction: column;
	}

	&__list {
		display: flex;
		flex-direction: column;
		margin: 0;
		padding: 0;
	}

	&__item {
		margin-bottom: 40px;

		&:last-child {
			margin-bottom: 52px;
		}

		@media screen and (min-width: 768px) {
			margin-bottom: 60px;
		}
	}

	&__link {
		text-decoration: none;
		font-style: normal;
		font-weight: 500;
		font-size: 28px;
		line-height: 1.21;
		color: $title-text-color;

		&:hover,
		:focus {
			padding: 10px 28px;
			background: linear-gradient(180deg, rgba(248, 110, 103, 0.63) 0%, rgba(253, 86, 143, 0.86) 100%);
			box-shadow: 0px 7px 27px -2px rgba(253, 88, 140, 0.35);
			border-radius: 0px;
			color: #ffffff;
		}
		@media screen and (min-width: 768px) {
			font-size: 40px;
			line-height: 1.22;
		}
	}
}

.main-nav {
	display: none;

	@media screen and (min-width: 1200px) {
		margin: 36px 0 65px 16px;
	}

	&__list {
		display: flex;
		margin: 0;
		padding: 0;
		list-style: none;
	}

	&__link {
		margin-right: 20px;
		padding: 4px 8px;
		text-decoration: none;
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		line-height: 1.22;
		color: $title-text-color;

		&--first {
			margin-left: 55px;
		}
		&--last {
			margin-right: 0;
		}

		&:hover,
		:focus {
			background: linear-gradient(180deg, rgba(248, 110, 103, 0.63) 0%, rgba(253, 86, 143, 0.86) 100%);
			box-shadow: 0px 7px 27px -2px rgba(253, 88, 140, 0.35);
			border-radius: 0px;
			color: #ffffff;
		}
	}
	@media screen and (min-width: 1200px) {
		display: flex;
	}
}
