body {
	font-family: "Montserrat", sans-serif;
	font-size: 16px;
	font-weight: 400;
	color: $primary-text-color;
	background: url(../images/fone.png) repeat center/cover #fbfbfb;
	&.is-hidden {
		overflow: hidden;
	}
}

.list {
	list-style: none;
	text-decoration: none;
	border: none;
	font-style: normal;
	margin: 0;
	padding: 0;
}

.visual-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.container {
	width: 100%;
	padding: 0 20px;
	margin: 0 auto;

	@media screen and (min-width: $tablet) {
		width: $tablet;
	}

	@media screen and (min-width: $desktop) {
		width: $desktop;
	}
}

.section {
	padding-top: 40px;
	padding-bottom: 40px;

	@media screen and (min-width: $tablet) {
		padding-top: 80px;
		padding-bottom: 80px;
	}

	@media screen and (min-width: $desktop) {
		padding-top: 100px;
		padding-bottom: 100px;
	}
}

.title {
	font-weight: 500;
	font-size: 32px;
	line-height: 1.2;
	text-align: center;
	margin: 0 0 28px 0;
}

.text {
	font-size: 18px;
	line-height: 1.22;
}

.button {
	display: flex;
	justify-content: center;
	width: 145px;
	padding: 8px 0;
	border-radius: 25px;
	border: transparent;
	cursor: pointer;

	box-shadow: 10px 15px 25px rgba(232, 183, 183, 0.45);
	background-color: $accent-color;

	font-size: 18px;
	line-height: 1.22;
	color: $primary-white;
	&:hover,
	:focus {
		color: $accent-color;
		background: #ffffff;
		border: 1px solid $accent-color;
	}
	&:active {
		color: $primary-white;
		background: $accent-color;
		border: 1px solid $primary-white;
	}
}
